.todo-app {
  margin: 20px 0px;
  text-align: center;
}
.list-maker {
  margin: 40px 0px;
  font-size: 30px;
  background-color: #feb139;
}
.maker-items {
  border-radius: 7px;
  display: flex;
  align-items: center;
}
.maker-items input {
  padding: 5px 0px;
  padding-left: 15px;
  padding-right: 15px;
  background-color: #5a94ff00;
  border: 0px;
  font-size: 18px;
  outline: none;
  color: white;
}
.maker-items i {
  cursor: pointer;
  font-weight: bold;
  color: #293462;
}
.maker-items i:hover {
  background-color: #fff80a;
  color: #293462;
}
.items-list {
    margin: 40px 0px;
}
.remove-all-button{

}
.button{
  font-size: 15px;
  font-weight: bold;
  outline: none;
  padding: 8px 16px;
  cursor: pointer;
  background-color: #E8AA42;
  border: 2px solid #E8AA42;
  color: #293462;
}
.button:hover{
  background-color: #293462;
  color: #E8AA42;
}
