.item {
  background-color: #d61c4e;
  margin: 7px 0px;
  padding-left: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}
.item:hover {
  background-color: #f87474;
}
.item-text {
  font-size: 18px;
}
.item-actions i {
  font-size: 25px;
  color: #293462;
}
.item-actions i:hover {
  cursor: pointer;
  background-color: #fff80a;
}
